.Item {
  width: 25%;

  background-size: 100%;
  border-radius: 20px;
  box-shadow: 15px 15px 15px rgba(31, 31, 31, 0.877), -2px -2px 20px rgba(0, 0, 0, 1); 
}

.rotateLeft {
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
}

.rotateRight {
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}

.Item__small img {
  width: 100%;
  margin-top: 40px;

  border:  3px solid gray;
  border-radius: 10px;

  -webkit-transition: all 1s;

  transition: all 1s;
}

.Item__small img:hover {
  box-shadow: 0px 0px 45px rgb(110, 110, 110);
  
  -webkit-transform: scale(1.2);
  
          transform: scale(1.2);
}

.Item__small span {
  margin-top: 35px;
  margin-bottom: 55px;

  text-align: center;
  color: white;
}

.Item__small {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Item > div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  height: 100%;

  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 20px;
}
.VisibleItems > div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;

  margin: 40px 5%;
}

.cntSliderFirst {
  -webkit-animation: slideinFirst 1s ease;
          animation: slideinFirst 1s ease;
}

.cntSliderTwo {
  -webkit-animation: slideinTwo 1s ease;
          animation: slideinTwo 1s ease;
}

@-webkit-keyframes slideinFirst {
  from {
    margin-left: 150%;
  }
  to {
    width: 90%;
  }
}

@keyframes slideinFirst {
  from {
    margin-left: 150%;
  }
  to {
    width: 90%;
  }
}

@-webkit-keyframes slideinTwo {
  from {
    margin-left: 150%;
  }
  to {
    width: 90%;
  }
}

@keyframes slideinTwo {
  from {
    margin-left: 150%;
  }
  to {
    width: 90%;
  }
}


.SwitchPanel {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  
  width: 100%;

  font-size: 4em;
}

.SwitchPanel div {
  margin: 40px;
  
  border-radius: 50%;
  
  -webkit-transition: 1s ease;
  
  transition: 1s ease;
}

.SwitchPanel div i,
.SwitchPanel div p {
  padding: 8px;

  text-align: center;
  vertical-align: middle;
}

.SwitchPanel div:first-child:hover,
.SwitchPanel div:last-child:hover {
  background-color: rgb(212, 123, 123);
  box-shadow: 0 0 10px rgba(0,0,0,0.5); 
  
  -webkit-transform: scaleX(1.2); 
  
          transform: scaleX(1.2);
}
.Slider {
  min-height: 600px;
  margin: 25px;

  border: 3px solid rgb(105, 105, 105);
  border-radius: 20px;
  background: -webkit-linear-gradient(45deg, rgb(136, 136, 136) 15%, rgb(100, 100, 100) 30%, rgb(223, 223, 223) 50%, rgb(100, 100, 100) 75%);
  background: linear-gradient(45deg, rgb(136, 136, 136) 15%, rgb(100, 100, 100) 30%, rgb(223, 223, 223) 50%, rgb(100, 100, 100) 75%);
  box-shadow: 0 0 10px rgba(0,0,0,1); 

  overflow: hidden;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


